.googleButton, .facebookButton {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  width: 100%;
  padding: 15px 0;
  margin-bottom: 16px;
  font-size: 20px;
  box-shadow: 2px 3px 3px rgb(0, 0, 0, 0.16);

  border:0;
  border-radius: 8px;
  cursor: pointer;
}

.googleButton {
  background: #fff;
  transition: all 0.5s ease-in-out;
}

.googleButton > svg {
  background-color: #fff;
  margin-right: 7px;
  transition: all 0.5s ease-in-out;
}

.googleButton:hover, .googleButton:hover > svg {
  background-color: #eee;
}

.facebookButton {
  background-color: #3C5997;
  color: #fff;
  transition: all 0.5s ease-in-out;
}

.facebookButton >svg {
  margin-right : 7px;
  background-color: #fff;
  transition: all 0.5s ease-in-out;
}

.facebookButton:hover, .facebookButton >svg:hover {
  background-color: #2B3F6B;
}

.isChecked {
  font-size: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 10px;
}

.isChecked > a {
  font-size: 15px;
  font-weight: 600;
}


.afterLogin{
  position: absolute;
  width:300px;
  background-color: #fff;
  z-index:5;
  right: 10px;

  padding: 20px;
  border-radius: 10px;
  border: 1px solid #ededed;
  
}

.Username {
  font-weight: 600;
}

.token {
